.sidebarContainer{
    width: 27%;
    
   
}

.cardHeader{

    text-transform: uppercasebv;
    font-size: 15px;
    text-align: center;
    
    box-sizing: border-box;
    letter-spacing: 2px;
    color: #333;
}

.profileImageContainer{
    width: 100%;
    
    box-sizing: border-box;
}
.profileImageContainer img{
    max-width: 100%;
    max-height: 100%;
    margin: 20px 0;
}


.personalBio{
    font-size: 14px;
    color: #565673;
    font-weight: 300;
}
.recentPost h3{
    
    color: #6c6c6c;
    letter-spacing: 1px;
    font-size: 14px;
    font-weight: 500;
    margin: 0;
}

.recentPost span{
    font-size: 10px;
    font-weight: 400;
    color: #6c6c6c;


}


.recentPost{
    border-bottom: 1px solid #eee;
    margin-bottom: 10px;
}

.recentPosts{
    padding: 20px 0;
    
}

